import * as styleConstants from  '../constants/StyleConstants';
import Section from './Section';
import Row from './Row'
export default () => {
    return <Section id="contact-us-container">
        <style jsx>{`
            #contact-us-container {
            }
            #contact-us {
                position:relative;
                display:block;
                visibility:hidden;
                top: -101px;
            }
        `}</style>
        
            <h1>CONTACT US</h1>
            <Row>
                <form>
                <input type="text" name="name" placeholder="Your Name"></input><br></br>
                <input type="email" name="email" placeholder="Email Address"></input><br></br>
                <textarea name="message" style="width:600px; height:200px;" placeholder="Message"></textarea><br></br>
                <input type="submit" value="Submit"></input>
                </form>
            </Row>
                
        </Section>
}